* {
    box-sizing: border-box;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #fafafa;
    color: #212121;
    line-height: 1.5;
}

#search-box {
    display: block;
    margin: 56px auto;
    font: inherit;
    padding: 8px;
    max-width: 320px;
    border: 1px solid rgba(33, 33, 33, 0.2);
    border-radius: 4px;
    outline: rgb(0, 54, 0);
    box-shadow: grey 2px 4px 7px 2px;
    border-color: green;
    cursor: pointer;
}

.country-info {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.country-info-item {
    font-weight: 700;
    font-size: 25px;
    list-style: none;
}

.title {
    font-size: 35px;
    margin: 15px 0px;
}

.country-info-value {
    font-weight: 400;
    font-size: 23px;
    margin-left: 10px;
}

.countries-list-item {
    padding: 5px 15px;
    /* margin-bottom: 5px; */
    display: flex;
    width: 300px;
    align-items: center;
    border-bottom: 1px solid green;
}

.country-name {
    margin-left: 10px;
}